.card{
	display: inline-block;
	background: linear-gradient(to bottom, #007bff, #16be94);
	color: #fff;
	width: 250px;
	height: 300px;
	padding: 0.7rem;
	margin: 0.5rem;
	font-size: 1.1em;
	box-shadow: 0 10px 10px rgba(0,0,0,0.5);
	transition-duration: 500ms;
	cursor: pointer;
	text-align: center;
}
.card:hover{
	transform: scale(1.1);
}
.card-info{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	white-space: normal;
}
.card-container{
	width: 100%;
	max-width: 1000px;
	margin: auto;
	padding: 2rem;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(300px,3fr));
	justify-items: center;
}
